import Vue from "vue";
import VueRouter from "vue-router";

import DispachLabel from "@/components/generateDispachLabel/DispachLabel"
import EndOfYearToast from "@/components/generateDispachLabel/EndOfYearToast"


Vue.use(VueRouter);
const routes = [
  {
    name: "dispachLabel",
    path: "/exceptiontoast",
    component: DispachLabel
  },
  {
    name: "Brinde Final de Ano",
    path: "/toast",
    component: EndOfYearToast
  }
];

const router = new VueRouter({
  mode: "history",
  routes
});

export default router;
