<template>
    <div class="dispach-label">
        <b-row>
            <b-btn ref="btnReport"  variant="secondary" @click="clickReport"
                                        type="button">Download Etiquetas</b-btn>
        </b-row>

        <b-modal ref="myModalRef" no-close-on-esc no-close-on-backdrop id="modal1" centered class="text-center"  hide-footer hide-header>
            <div v-show="loadingImport" style="width: 100%; text-align: center;">
                <img alt="" src="@/assets/loading-image.gif">
            </div>
        </b-modal>

    </div>
</template>

<script>
import {baseApiUrl} from "@/global"
import axios from "axios"


export default {
    name: "DispachLabel",
    data() {
        return {
            loading: true,
            loadingImport: true,
            linkPrint: ''
        }
    },
    methods: {
        clickReport : function () {
            this.loadingImport = true;
            // this.linkPrint = `${baseApiUrl}/exception/printlabel`
            this.$refs.myModalRef.show();
            axios.get(`${baseApiUrl}/api/printlabel64`).then(res=> {
                var sampleArr = base64ToArrayBuffer(res.data);
                saveByteArray("etiquetas", sampleArr);
                this.loadingImport = true;
                this.$refs.myModalRef.hide();
            })
        }
    }
}
function base64ToArrayBuffer(base64) {
    var binaryString = window.atob(base64);
    var binaryLen = binaryString.length;
    var bytes = new Uint8Array(binaryLen);
    for (var i = 0; i < binaryLen; i++) {
       var ascii = binaryString.charCodeAt(i);
       bytes[i] = ascii;
    }
    return bytes;
 }

 function saveByteArray(reportName, byte) {
    var blob = new Blob([byte], {type: "application/zip"});
    var link = document.createElement('a');
    link.href = window.URL.createObjectURL(blob);
    var fileName = reportName;
    link.download = fileName;
    link.click();
 }
</script>

<style>
.text-center {
	text-align: center;
}
</style>
